@import '../styles.scss';


.container { @include border($radius: 4px); @include vertically-centred; position: relative; height: auto; cursor: default; min-height: 2.5rem; max-height: 100%; justify-content: space-between; }
  .select { margin: 0.6rem; display: flex; justify-content: space-between; width: 98%; }
    .select-value { max-width: 70%; }
      .select-value  p { white-space: nowrap; overflow: hidden; text-overflow: ellipsis;  }
    .icons { display: flex; margin: 0 1rem; }
      .icons small { @include bg-color($primary-icon-color); width: 1.5px; margin: 0 1.5rem; }
      .icon { @include color($secondary-icon-color); @include vertically-centred; transition: 0.2s ease-in-out color; }
      .icon:hover { @include color($primary-icon-color); cursor: pointer; }
      .icon.arrow { position: relative; transition: 0.4s ease-in-out transform, 0.4s ease-in-out padding; }
      .icon.arrow.open { @include color($primary-icon-color); transform: rotate(180deg); }
  .options { @include border($radius: 4px); @include fade('hide'); @include position($z-index: 20); width: 100%; max-height: 0; overflow: hidden; }
  .options::-webkit-scrollbar-track { @include theme($theme) { box-shadow: inset 0 0 5px get-theme-color($scroll-color); }; border-radius: 10px; margin: 0 30px; }
  .options::-webkit-scrollbar-thumb { @include bg-color($scroll-color); border-radius: 10px; }
  .options::-webkit-scrollbar { width: 5px; }
  .options.open { @include bg-color(); @include fade('show'); }
  .search { width: 70%; margin: 0.5rem 0 1rem 0.5rem; }
  .search-msg { width: 80%; margin: 1rem auto; text-align: center; }
.errmsg { color: $danger-color; width: 99%; font-size: 1.3rem; margin: 0 0 0 5px; }
.multiOptions { margin-top: 1rem; flex-wrap: wrap; padding: 0.5rem; display: flex; }
