@import '../styles.scss';


.avatar { display: inline-block; border-radius: 32px; cursor: pointer; }
.avatar.small { width: 32px; height: 32px; }
.avatar.medium { width: 52px; height: 52px; }
.avatar.large { width: 64px; height: 64px; }
	.image { width: 100%; height: 100%; display: block; margin: 0; border-radius: 32px; }
	.placeholder { @include color($white-text-color); @include vertically-centred; width: 100%; height: 100%; justify-content: center; background-repeat: no-repeat; background-position: center; background-size: 120%; font-size: 20px; font-weight: bold; text-align: center; border-radius: 32px; }
