@import '../styles.scss';


.input { width: 100%; height: auto; position: relative; }
  .label.top { bottom: 2.5rem; }
  .input.medium .inputtag { height: 3rem; font-size: 1.5rem; }
  .input.medium .label { bottom: 0.1rem; font-size: 1.5rem; }
    .input.medium .label.top { bottom: 2.6rem; }
  .input.large .inputtag { height: 3.5rem; font-size: 1.6rem; }
  .input.large .label { bottom: 0.15rem; font-size: 1.6rem; }
    .input.large .label.top { bottom: 2.85rem; left: 0.1rem; }
  .input.large.inside .inputtag { padding-top: 1.5rem; }
  .input.large.inside .label.top { font-size: 1.1rem; left: 0.5rem; bottom: 1.5rem; }
  .input.large.inside .label.in { left: 3.5rem; }
  .icon { @include bg-color($accent-color); @include position(absolute, 0.8px, 0.9px, 0.7px, null, 1); @include vertically-centred; width: 3rem; padding: 0 0.5rem; }
    .icon.left { left: 0.74px; }
    .icon.left + .label { left: 3.5rem; }
    .icon.left + input, 
    .icon.left + .label + input { padding-left: 3.4rem; }
  .label { @include position(absolute, null, null, -0.1rem, 0.7rem); @include multiple-transition(0.2s, bottom, left, font-size); width: 97%; font-size: 1.4rem; pointer-events: none; margin-bottom: 0.5rem; }
    .label.top {  left: 0.1rem; bottom: 2.2rem; }
  .inputtag { @include bg-color(); @include color(); @include border($radius: 2px); width: 100%; padding: 0.2rem; }
  .inputtag[type=number]::-webkit-inner-spin-button { -webkit-appearance: none; } /*remove default strokes */
  .inputtag[type=number] { -moz-appearance: textfield; }
  .errmsg { color: $danger-color; width: 99%; font-size: 1.3rem; margin: 0.2rem 0 0 0.2rem; }
  