@import '../styles.scss';


.phone-input { display: flex; }
  .phone-input .select { width: 9rem; height: 3rem; border-radius: 0; border-bottom-left-radius: 3px; border-top-left-radius: 3px; }
    .custom-temp-opts { @include vertically-centred; width: 83%;justify-content: space-between; }
      .custom-temp-opts img { width: 26px; height: 16px; margin-right: 0.5rem; }
      .custom-temp-opts > span { text-align: center; margin-right: 0.5rem; }
  .input { @include border($radius: 0); padding-left: 1rem; height: 3rem; border-left: none !important; border-top-right-radius: 3px !important; border-bottom-right-radius: 3px !important; }
.errmsg { color: $danger-color; width: 99%; font-size: 1.3rem; margin: 0 0 0 5px; }

.country-flags { @include border($size: 0.5); width: 26px; height: 16px; margin-right: -1rem; margin-bottom: -0.3rem; }