@import '../styles.scss';


.section { @include border($radius: 4px); margin-bottom: 0.4rem; overflow: hidden; }
.section.horizontal-section { display: flex; margin-right: 0.5rem; }
    .section.horizontal-section .title { justify-content: center; width: 4rem; display: flex; flex-direction: column; }
      .section.horizontal-section .title.title-open { border-bottom: none !important; @include border(right); }
      .section.horizontal-section .title p { writing-mode: vertical-rl; transform: rotate(180deg); } 
    .section.horizontal-section .horizontal-children { @include multiple-transition(0.7s, max-width, margin); overflow: hidden;  }

  .title { display: flex; justify-content: space-between; vertical-align: middle; padding: 0.5rem; cursor: pointer; transition: 0.2s ease-in background-color; }
  .title:hover { @include bg-color($accent-color); }
  .title.title-open { @include border(bottom); @include bg-color($accent-color); }
    .arrow { @include color($primary-icon-color); @include vertically-centred; margin-right: 0.3rem; font-size: 15px; }
      .open { transition: 0.4s ease-in-out; transform: rotate(180deg); }
      .close { transition: 0.4s ease-in-out; transform: rotate(0deg); }
  .children { @include multiple-transition(0.5s, max-height, margin-top); margin: 0.5rem 1rem; overflow: hidden; }